:root {
  /* Colors */
  --clr-primary-100: hsl(7deg, 99%, 70%);
  --clr-primary-200: hsl(51deg, 100%, 49%);
  --clr-primary-300: hsl(167deg, 40%, 24%);
  --clr-primary-400: hsl(198deg, 62%, 26%);
  --clr-primary-500: hsl(168deg, 34%, 41%);
  --clr-neutral-100: hsl(212deg, 27%, 19%);
  --clr-neutral-200: hsl(213deg, 9%, 39%);
  --clr-neutral-300: hsl(232deg, 10%, 55%);
  --clr-neutral-400: hsl(210deg, 4%, 67%);
  --clr-neutral-500: hsl(0deg, 0%, 100%);
  /* Font Families */
  --ff-primary: "Barlow", sans-serif;
  --ff-secondary: "Fraunces", serif;
  /* Font Sizes */
  --fs-body: clamp(1rem, 0.9107142857rem + 0.0035714286 * 100vw, 1.125rem);
  --fs-h1: clamp(2.25rem, 1.8928571429rem + 0.0142857143 * 100vw, 2.75rem);
  --fs-h2: clamp(1.75rem, 1.3928571429rem + 0.0142857143 * 100vw, 2.25rem);
  --fs-h3: clamp(1.625rem, 1.5357142857rem + 0.0035714286 * 100vw, 1.75rem);
  /* Font Weights */
  --fw-semibold: 600;
  --fw-bold: 700;
  --fw-black: 900;
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
body {
  font-family: var(--ff-primary);
  font-size: var(--fs-body);
  font-weight: var(--fw-semibold);
}

h1, h2, h3 {
  font-family: var(--ff-secondary);
}

h1 {
  font-size: var(--fs-h1);
}

h2 {
  font-size: var(--fs-h2);
}

h3 {
  font-size: var(--fs-h3);
}

.container {
  max-width: 90rem;
  margin-left: auto;
  margin-right: auto;
}

.grid {
  display: grid;
  gap: var(--gap, 1rem);
}

.flex {
  display: flex;
  gap: var(--gap, 1rem);
}

.header {
  background-color: #3dbeff;
}
.header__wrapper {
  background-image: url("/assets/images/mobile/image-header.jpg");
  background-position: top center;
  background-size: cover;
  background-repeat: no-repeat;
  justify-content: space-between;
}
@media (min-width: 60em) {
  .header__wrapper {
    background-image: url("/assets/images/desktop/image-header.jpg");
  }
}
.header .nav__item {
  display: inline-block;
}

.fem-attribution {
  font-size: 11px;
  text-align: center;
  margin-top: 2rem;
}

.fem-attribution a {
  color: hsl(228deg, 45%, 44%);
}